.App {
  text-align: left;
}

.done-button {
  background-color: lightgrey;
  padding: 15px;
  margin: 10px;
  font-size: 24px;
  border-radius: 5px;
  user-select: none;
  border: none;
  box-shadow:
    0px 1px 3px 0px rgba(0,0,0,0.2),
    0px 1px 1px 0px rgba(0,0,0,0.14),
    0px 2px 1px -1px rgba(0,0,0,0.12);
}

.done-button:focus {
  outline: none;
}

.done-button:active {
  box-shadow: none;
  border: 2px inset lightgray;
}

.card {
  min-width: 300px;
  min-height: 80px;

  margin: 10px;

  box-shadow:
  0px 1px 3px 0px rgba(0,0,0,0.2),
  0px 1px 1px 0px rgba(0,0,0,0.14),
  0px 2px 1px -1px rgba(0,0,0,0.12);
}

.form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.done-container {
  flex-basis: 100%;
  display: flex;
  justify-content: center;
  position: relative;
}

.done-container input {
  position: absolute;
  
  bottom: 2px;
  left: 2px;
}

.swanky-input-container {
  position: relative;
}

.swanky-input-container input {
  position: absolute;
  
  bottom: 2px;
  left: 2px;

  border: none;
  border-bottom: 1px solid black;

  width: calc(100% - 14px);
  height: 60px;

  font-size: 22px;
  padding-left: 10px;
  outline: none;
  transition: font-size 0.25s;
}

.swanky-input-container input:focus {
  outline: none;
  font-size: 20px;
  border-bottom: 2px solid green;
}

.swanky-input-container span.title {
  position: absolute;
  top: 5px;
  left: 5px;
  cursor: pointer;
  
  font-weight: 300;
  font-size: 14px;

  user-select: none;

  z-index: 15;

  transition: color 1s, font-size 0.25s;

}

.swanky-input-container input:focus + span.title,
.swanky-input-container:hover span.title {
  color: green;
  font-size: 12px;
}

.email-invalid {
  position: absolute;
  top: 5px;
  right: 5px;

  font-size: 12px;
  color: red;
  text-align: right;
}

.swanky-input-container input:focus ~ span.email-invalid {
  display: none;
}

.swanky-input-container input:not(:focus) ~ span.email-invalid {
  position: absolute;
  top: 5px;
  right: 5px;

  font-size: 12px;
  color: red;
  text-align: right;
}

.swanky-input-container .react-datepicker__input-container {
  position: static;
}

.done-button:hover {
  background-color: #eee;
}

.modal-open {
  position: fixed;
  top: 20vh;
  height: 40vh;
  left: 20vw;
  width: 60vw;
  background-color: lightgrey;
  border-radius: 5px;
  padding: 8px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.modal-open button {
  background-color: white;
  border-radius: 5px;
  stroke-width: 3px;
}

.modal-open .x-button {
  fill: white;
  stroke: black;
  stroke-width: 2px;

  width: 20px;
  height: 20px;

  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-open .x-button circle,
.modal-open .x-button path {
  cursor: pointer;
}

.modal-closed{
  display: none;
}

.modal-open svg.x-button *:hover

.modal-closed {
  display: none;
}

.modal-open .x-button:hover path {
  stroke: red;
}

.modal-shade{
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;

  z-index: 420;
  background-color: #3338;
}

.goldRecords {
  position: absolute;
  right: 50px;
  height: 100%;
  
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

.goldRecord {
  width: 20px;
  z-index: 20;
}

.goldRecord img {
  width: 50px;
  height: 50px;
}

.album-dropdown-base {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;

  padding: 30px 25px 10px 10px;

  padding-top: 30px;
  padding-right: 15px;
}

.album-dropdown-base img {
  height: 45px;
  margin-top: -5px;
  width: 45px;
}

.album-dropdown-base .dropdown-arrow {
  position: absolute;
  right: 5px;
  user-select: none;
}

ul.album-menu {
  list-style: none;
  padding: 0;
  position: absolute;
  top: 100%;
  max-height: 25vh;
  overflow-y: auto;margin: 2px 0 0 0;

  z-index: 30;

  background-color: white;
  box-shadow:
    0px 1px 3px 0px rgba(0,0,0,0.2),
    0px 1px 1px 0px rgba(0,0,0,0.14),
    0px 2px 1px -1px rgba(0,0,0,0.12);
  
}

ul.album-menu li:hover {
  background-color: #eee;
}

.album-dropdown-base,
ul.album-menu li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  padding: 10px;
  position: relative;
  z-index: 30;
  background-color: white;
}

.album-dropdown-base img,
ul.album-menu img {
  height: 45px;
  margin-top: -5px;
  width: 45px;
}

.album-dropdown-base .dropdown-arrow {
  position: absolute;
  right: 5px;
  user-select: none;
}

.album-dropdown-base span:not(.dropdown-arrow),
ul.album-menu li span {
  display: inline-block;
  width: 180px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.album-dropdown-base span:not(.dropdown-arrow):nth-child(2),
ul.album-menu li span:nth-child(2) {
  width: 38px;
  padding: 0 3px 0 9px;
}